import { useFormik } from "formik";
import { useDispatch, useSelector } from "react-redux";
import Input from "../../components/Input";
import InputSelect from "../../components/InputSelect";
import { saveProfile } from "../../redux/profileSlise";
import { State } from "../../redux/types";
import { calcAllMetabolism, calcMetabolism } from "../../utils/math";
import {
  optionsActivity,
  optionsTarget,
  optionsSex,
} from "../../utils/optionsLists";
import { ProfileInputValues } from "../../utils/types";
import { profileValidationSchema } from "../../utils/validation";
import {
  Inputs,
  ProfileContainer,
  ProfileForm,
  Result,
  Results,
  ResultTitle,
  ResultUnit,
  SaveButton,
  Title,
} from "./Profile.styles";

function Profile() {
  const dispatch = useDispatch();
  const data = useSelector((state: State) => state.profile);

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      name: data.name,
      age: data.age,
      stature: data.stature,
      weight: data.weight,
      sex: data.sex,
      activityLevel: data.activityLevel,
      target: data.target,
    } as ProfileInputValues,
    validationSchema: profileValidationSchema,
    onSubmit: (values) => {
      dispatch(saveProfile({ ...values, baseMetabolism, targetMetabolism }));
    },
  });

  const { name, age, stature, weight, sex, activityLevel, target } =
    formik.values;

  const { baseMetabolism, activeMetabolism, targetMetabolism } =
    calcMetabolism(formik.values);

  return (
    <ProfileContainer>
      <ProfileForm onSubmit={formik.handleSubmit}>
        <Input
          formik={formik}
          value={name}
          name="name"
          placeholder="–ò–º—è"
          type={"text"}
        />
        <Title>
          –í–≤–µ–¥–∏—Ç–µ –¥–∞–Ω–Ω—ã–µ —á—Ç–æ–±—ã —Ä–∞—Å—Å—á–∏—Ç–∞—Ç—å –¥–Ω–µ–≤–Ω—É—é –Ω–æ—Ä–º—É –∫–∞–ª–æ—Ä–∏–π–Ω–æ—Å—Ç–∏
        </Title>
        <Inputs>
          <Input
            formik={formik}
            value={age}
            name="age"
            placeholder="–í–æ–∑—Ä–∞—Å—Ç"
            type={"number"}
          />
          <Input
            formik={formik}
            value={stature}
            name="stature"
            placeholder="–†–æ—Å—Ç –≤ —Å–º"
            type={"number"}
          />
          <Input
            formik={formik}
            value={weight}
            name="weight"
            placeholder="–í–µ—Å –≤ –∫–≥"
            type={"number"}
          />
        </Inputs>
        <InputSelect
          formik={formik}
          value={!sex ? "" : optionsSex.find((o) => o.value === sex)!.text}
          options={optionsSex}
          name="sex"
          placeholder="–ü–æ–ª"
        />
        <InputSelect
          formik={formik}
          value={
            !activityLevel
              ? ""
              : optionsActivity.find((o) => o.value === activityLevel)!.span
          }
          options={optionsActivity}
          name="activityLevel"
          placeholder="–£—Ä–æ–≤–µ–Ω—å –∞–∫—Ç–∏–≤–Ω–æ—Å—Ç–∏"
        />
        <InputSelect
          formik={formik}
          value={
            !target
              ? ""
              : optionsTarget.find((o) => o.value === target)!.text
          }
          options={optionsTarget}
          name="target"
          placeholder="–¶–µ–ª—å"
        />
        <Title>–†–µ–∑—É–ª—å—Ç–∞—Ç—ã —Ä–∞—Å—Å—á—ë—Ç–∞:</Title>
        <Results>
          <ResultTitle>–ë–∞–∑–æ–≤—ã–π –º–µ—Ç–∞–±–æ–ª–∏–∑–º:</ResultTitle>
          <Result>{baseMetabolism}</Result>
          <ResultUnit>–∫–∫–∞–ª</ResultUnit>
          <ResultTitle>–° —É—á—ë—Ç–æ–º –∞–∫—Ç–∏–≤–Ω–æ—Å—Ç–∏:</ResultTitle>
          <Result>{activeMetabolism}</Result>
          <ResultUnit>–∫–∫–∞–ª</ResultUnit>
          <ResultTitle>–î–ª—è –±—ã—Å—Ç—Ä–æ–≥–æ ‚Üì –≤–µ—Å–∞:</ResultTitle>
          <Result>{calcAllMetabolism(0.8, activeMetabolism)}</Result>
          <ResultUnit>–∫–∫–∞–ª</ResultUnit>
          <ResultTitle>–î–ª—è —Å–Ω–∏–∂–µ–Ω–∏—è –≤–µ—Å–∞:</ResultTitle>
          <Result>{calcAllMetabolism(0.9, activeMetabolism)}</Result>
          <ResultUnit>–∫–∫–∞–ª</ResultUnit>
          <ResultTitle>–î–ª—è –Ω–∞–±–æ—Ä–∞ –≤–µ—Å–∞:</ResultTitle>
          <Result>{calcAllMetabolism(1.1, activeMetabolism)}</Result>
          <ResultUnit>–∫–∫–∞–ª</ResultUnit>
          <ResultTitle>–î–ª—è –±—ã—Å—Ç—Ä–æ–≥–æ ‚Üë –≤–µ—Å–∞:</ResultTitle>
          <Result>{calcAllMetabolism(1.2, activeMetabolism)}</Result>
          <ResultUnit>–∫–∫–∞–ª</ResultUnit>
        </Results>
        <SaveButton
          type="submit"
          aria-label="–î–æ–±–∞–≤–∏—Ç—å –∑–∞–ø–∏—Å—å"
          disabled={!formik.isValid}
        >
          –°–æ—Ö—Ä–∞–Ω–∏—Ç—å
        </SaveButton>
      </ProfileForm>
    </ProfileContainer>
  );
}

export default Profile;
